<template>
  <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
    <div class="vist-customer flex flex-column">
        <div class="partner-distribution flex flex-1 flex-column">
            <div class="sub-title">
                <div class="sub-title-icon pie-icon"></div>
                <span class="sub-title-name">合作伙伴拜访情况</span>
            </div>
            <div class="vist-customer-middle flex flex-11 flex-row">
              <e-pie class="vist-e-pie flex" :id="'e-pie-1'" :title="'客户数占比'" :pieConfig="pieConfig1" :data="eData1"></e-pie>
              <e-pie class="vist-e-pie flex" :id="'e-pie-2'" :title="'拜访次数占比'" :pieConfig="pieConfig2" :data="eData2"></e-pie>
            </div>
            <div class="vist-customer-bottom">
              <data-table orderBy="1|desc" :scrollY="163" :title="table.tableTitle" :colorList="table.colorList" :data="table.tableData"></data-table>
            </div>
        </div>
        <div class="industry-distribution flex-1">
            <div class="sub-title flex-1">
                <div class="sub-title-icon pie-icon"></div>
                <span class="sub-title-name">用户拜访情况</span>
            </div>
            <div class="vist-customer-middle flex flex-11 flex-row">
              <e-pie class="vist-e-pie flex" :id="'e-pie-3'" :title="'客户数占比'" :pieConfig="pieConfig1" :data="eData1"></e-pie>
              <e-pie class="vist-e-pie flex" :id="'e-pie-4'" :title="'拜访次数占比'" :pieConfig="pieConfig2" :data="eData2"></e-pie>
            </div>
            <div class="vist-customer-bottom">
              <data-table orderBy="1|desc" :scrollY="163" :title="table.tableTitle" :colorList="table.colorList" :data="table.tableData"></data-table>
            </div>
        </div>
    </div>
  </van-pull-refresh>
</template>

<script>
import { PullRefresh } from "vant";
import ePie from "./common/echarts-pie";
import { eData1, eData2 } from "./data/echarts-pie-data.js";
import dataTable from "./common/datatables";

export default {
  name: "overview",
  components: {
    [PullRefresh.name]: PullRefresh,
    ePie,
    dataTable
  },
  data() {
    return {
      isLoading: false,
      eData1,
      eData2,
      pieConfig1: {
        imgUrl: require("@/assets/image/pie-customer.png"),
        size: "50px",
        labelShow: false,
        radius:['50%','70%']
      },
      pieConfig2: {
        imgUrl: require("@/assets/image/pie-customer-percent.png"),
        size: "80px",
        labelShow: false,
        radius:['50%','70%']
      },
      table:{
        tableTitle: ["客户名称", "客户数", "拜访次数"],
        colorList:["#448DFF", "#59D4FF", "#751DE2", "#BB2B79", "#DB479B", "#F8B9D9", "#D391B8", "#C2A0F7", "#AEEFFE", "#A7D0FF"],
        tableData: [
          ["客户名称1", "45%", "45"],
          ["客户名称2", "35%", "22"],
          ["客户名称3", "25%", "43"],
          ["客户名称4", "15%", "12"],
          ["客户名称5", "45%", "3"],
          ["客户名称5", "45%", "3"],
          ["客户名称5", "45%", "3"],
          ["客户名称5", "45%", "3"],
          ["客户名称5", "45%", "3"],
          ["客户名称5", "45%", "3"],
          ["客户名称5", "45%", "3"],
          ["客户名称5", "45%", "3"]
        ]
      },
    };
  },
  mounted() {
    this.$parent.active = this.findKey(this.$parent.pageMap, this.$route.name);
  },
  methods: {
    findKey(obj, value, compare = (a, b) => a === b) {
      return Object.keys(obj).find(k => compare(obj[k], value));
    },
    onRefresh() {
      setTimeout(() => {
        this.isLoading = false;
        this.$router.replace({
          path: "/refresh",
          query: {
            t: Date.now()
          }
        });
      }, 500);
    }
  }
};
</script>

<style scoped>
.vist-customer {
  width: 100%;
  height: calc(100% - 120px);
  overflow: hidden;
}

.vist-customer .partner-distribution {
  min-height: 320px;
}

.vist-customer .partner-distribution-icon {
  background-image: url("../assets/image/vist-customer-icon.png");
}

.vist-customer .industry-distribution {
  min-height: 320px;
}

.vist-customer-middle {
  min-height: 200px;
}

.vist-customer-bottom {
  min-height: 200px;
  margin: 0 40px 10px 40px;
}

</style>
